<template>
  <section
    fluid
    class="colours">
    <h1>Colours</h1>
    <p>
      Here is a a bubble chart showing all the HTML Colour names.
      It was created just as an experiment using <a href="https://d3js.org/">d3.js</a> for rendering.
    </p>
    <label>Charge
      <input
        v-model="charge"
        type="number"
        required
        @change="updateSimulation" >
    </label>
    <label>Min Size
      <input
        v-model="minSize"
        min="0"
        required
        type="number"
        @change="updateSimulation" >
    </label>
    <label>Max Size
      <input
        v-model="maxSize"
        min="0"
        required
        type="number"
        @change="updateSimulation" >
    </label>
    <button
      type="button"
      @click="add">Add</button>
    <button
      type="button"
      @click="save">Save</button>
    <button
      type="button"
      @click="reset">Reset</button>
    <div id="bubble-chart"/>
  </section>
</template>
<script>
import * as d3 from 'd3'
import namedColors from '@/assets/namedColors'

export default {
  name: 'Colours',
  components: {},
  data () {
    return {
      windowWidth: 0,
      offset: {
        x: 0,
        y: 0
      },
      width: 600,
      height: 400,
      charge: -20,
      minSize: 10,
      maxSize: 30,
      data: [
        {
          id: Math.random(),
          title: 'Type of thing1',
          category: 'Foo',
          count: 48
        },
        {
          id: Math.random(),
          title: 'Type of thing2',
          category: 'Foo',
          count: 1922
        },
        {
          id: Math.random(),
          title: 'Type of thing3',
          category: 'Foozy',
          count: 999
        },
        {
          id: Math.random(),
          title: 'Another',
          category: 'Bar',
          count: 1700
        },
        {
          id: Math.random(),
          title: 'Something else',
          category: 'Bar',
          count: 1000
        },
        {
          id: Math.random(),
          title: 'Something else is here',
          category: 'Bar',
          count: 2200
        }
      ]
    }
  },
  watch: {
    windowWidth (newWidth, oldWidth) {
      this.updateNodes()
    }
  },
  mounted () {
    this.$nextTick(() => {
      window.addEventListener('resize', () => {
        this.windowWidth = window.innerWidth
      })
    })
    this.load()
  },
  methods: {
    load () {
      this.data = JSON.parse(JSON.stringify(namedColors))
      const loadedData = localStorage.getItem('bubbles')
      if (loadedData) this.data = JSON.parse(loadedData)
      this.initBubbleChart()
    },
    add () {
      this.data.push({
        id: Math.random(),
        title: 'More things',
        category: 'More',
        count: 500,
        x: Math.random() * 10000,
        y: Math.random() * 10000
      })

      this.updateNodes()
    },
    save () {
      localStorage.setItem('bubbles', JSON.stringify(this.data))
    },
    reset () {
      localStorage.removeItem('bubbles')
      this.svg.node().remove()
      this.$nextTick(() => {
        this.load()
      })
    },
    updateSimulation () {
      const scaleRadius = d3
        .scaleLinear()
        .domain([
          d3.min(this.data, d => d.count),
          d3.max(this.data, d => d.count)
        ])
        .range([this.minSize, this.maxSize])

      if (this.simulation) {
        this.simulation.nodes(this.data)
        this.simulation.alpha(1).restart()
      } else {
        this.simulation = d3.forceSimulation(this.data)
      }

      this.simulation
        .force('charge', d3.forceManyBody().strength(this.charge))
        .force('x', d3.forceX())
        .force('y', d3.forceY())
        .force('collide', d3.forceCollide((d) => (scaleRadius(d.count) || 10) + 1).iterations(24))

        .on('tick', e => {
          this.node
            .attr('r', d => scaleRadius(d.count) || 10)
            .attr('cx', d => d.x + this.offset.x)
            .attr('cy', d => d.y + this.offset.y)
        })
    },
    initBubbleChart () {
      const chart = d3.select('#bubble-chart')

      this.tooltip = chart
        .append('div')
        .attr('class', 'tooltip')
        .text('')

      this.colorCircles = d3.scaleOrdinal(d3.schemeCategory10)

      this.svg = chart
        .append('svg')
        .attr('width', '100%')
        .attr('height', '500')

      this.node = this.svg
        .selectAll('circle')

      this.updateNodes()
    },
    updateNodes () {
      this.node = this.node.data(this.data, d => d.name || d.title)
      this.node.exit().remove()

      const bounds = this.svg.node().getBoundingClientRect()
      this.$set(this.offset, 'x', bounds.width / 2)
      this.$set(this.offset, 'y', bounds.height / 2)
      this.node = this.node.enter()
        .append('circle')
        .style('fill', d => d.color || this.colorCircles(d.category))
        .on('mouseover', d => {
          if (d.dragging) return
          this.tooltip
            .html(this.getTooltipText(d))
            .style('opacity', 0.9)
            .style('z-index', 100)
        })
        .on('mousemove', () => this.tooltip
          .style('top', d3.event.pageY - 10 + 'px')
          .style('left', d3.event.pageX + 10 + 'px'))
        .on('mouseout', () => this.tooltip
          .style('opacity', 0)
          .style('z-index', -1))
        .call(d3.drag()
          .on('start', (d) => {
            d.dragging = true
            this.tooltip
              .style('opacity', 0)
              .style('z-index', -1)

            if (!d3.event.active) this.simulation.alpha(1).restart()
            d.fx = d.x
            d.fy = d.y
          })
          .on('drag', (d) => {
            if (!d3.event.active) this.simulation.alpha(1).restart()
            d.fx = d3.event.x
            d.fy = d3.event.y
          })
          .on('end', (d) => {
            d.dragging = false
            d.fx = null
            d.fy = null
          }))
        .merge(this.node)

      this.updateSimulation()
    },
    getTooltipText (d) {
      return d.name
      // return '' +
      // '<div>' + (d.name || d.title) + '</div>'         +
      // '<div><strong>' + d.count + '</strong></div>' +
      // '<div><em>' + d.category + '</em></div>'
    }
  }
}
</script>

<style lang="scss">
.colours {
  .tooltip {
    position: absolute;
    color: white;
    padding: .3em;
    background-color: #999;
    border-radius: .5em;
    transition: opacity .4s;
    top: -9999;
    left: 0;
    opacity: 0;
    z-index: -1;
  }
}
</style>



